import DashboardPage from "@/Components/DashboardPage/dashboard";

const [menuOpen, setMenuOpen] = useState(false);

const toggleMenu = () => setMenuOpen(!menuOpen);

const renderPageContent = () => {
  switch (activePage) {
    case "dashboard":
      return <DashboardPage />;
    default:
      return null;
  }
};

function App(){
  return(
    <div>
      <p>James</p>

      <button title="dashboard" isActive={activePage === 'dashboard'} onClick={() => setActivePage('dashboard')} className="lg:hidden text-white focus:outline-none">
        {menuOpen ? <X className="h-8 w-8" /> : <Menu className="h-8 w-8" />}  

        DashboardPage

      </button>
    </div>
  )
}

export default App;